.button__top::after {
  background-color: #fff;
  border: 0.0625rem solid #408F95;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button__top:hover {
  color: #408F95;
}

.button__top:hover::after {
  transform: scale(1, 1);
}

.button__top:hover .button__top__span::before,
.button__top:hover .button__top__span::after {
  background-color: #408F95;
}

.button__top span {
  position: relative;
}

.button__top span::before {
  content: "";
  position: absolute;
  top: 46%;
  right: -3.25rem;
  transform: translateY(-50%) rotate(42deg);
  width: 0.625rem;
  height: 0.0625rem;
  background-color: #fff;
}
@media screen and (min-width: 768px) {
  .button__top span::before {
    right: -3.0625rem;
    top: 40%;
  }
}

.button__top span::after {
  content: "";
  position: absolute;
  top: 58%;
  right: -3.1875rem;
  transform: translateY(-50%);
  width: 2.625rem;
  height: 0.0625rem;
  background-color: #fff;
}
@media screen and (min-width: 768px) {
  .button__top span::after {
    width: 2.6875rem;
    right: -3.125rem;
    top: 55%;
