    background-size: contain;
    content: "";
    display: block;
    height: 5rem;
    margin: auto;
    width: 5rem;
  }
  /* 前への矢印カスタマイズ */
  .swiper-button-prev::after {
    background-image: url(../images/only_pc/allow_left.svg);
  }
  /* 次への矢印カスタマイズ */
  .swiper-button-next::after {
    background-image: url(../images/only_pc/allow_right.svg);
  }
}
