/* page_top */
#to_top {
  display: block;
  position: fixed;
  right: 0.9375rem;
  bottom: 1rem;
  cursor: pointer;
  z-index: 100;
  background-color: #fff;
  width: 3rem;
  height: 3rem;
  box-sizing: border-box;
  border: solid 0.0625rem #408F95;
  border-radius: 50%;
}
@media screen and (min-width: 768px) {
  #to_top {
    right: 1.25rem;
    bottom: 1.25rem;
    width: 5rem;
    height: 5rem;
    box-shadow: 0 0.25rem 0.1875rem rgba(0, 0, 0, 0.1198);
  }
}

#to_top::before {
  content: "";
  position: absolute;
  left: 20%;
  top: 50%;
  width: 1.75rem;
  border-top: solid 0.0625rem #408F95;
  transform: rotate(90deg);
}
@media screen and (min-width: 768px) {
  #to_top::before {
    top: 50%;
    left: 25%;
    width: 2.5rem;
  }
}

#to_top::after {
  content: "";
  position: absolute;
  border-top: solid 0.0625rem #408f95;
  transform: rotate(-50deg);
  top: 26%;
  left: 35%;
  width: 0.5625rem;
}
@media screen and (min-width: 768px) {
  #to_top::after {
    top: 30%;
    left: 40.4%;
    width: 0.625rem;
  }
}

#to_top:hover {
  opacity: 0.7;
}

